body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  width: 800px;
  margin: 20px;
}


p, form, ul {
  padding: 20px;
}

header {
  padding: 20px;
  border-bottom: 1px solid rgb(61, 85, 104);
  background-color: rgb(61, 85, 104);
  color: white;
}

.title {
  font-weight: bold;
  font-size: 1.5em;
}


.alert {
  border: 0;
  padding: 0px;
}

.alert>* {
  border-color: red;
  border-width: 2px;
  margin: 0px;
}

.alert>*>header {
  border-color: red;
  background-color: rgb(104, 61, 61);
}

.alert-symbol {
  display: block;
  width: 40px;
  text-align: center;
  background-color: red;
  color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.alert:hover>* {
  border-color: rgb(255, 94, 94);
  border-width: 2px;
  margin: 0px;
}

.alert:hover>*>header {
  border-color: rgb(255, 94, 94);
  background-color: rgb(104, 79, 79);
}

.alert:hover .alert-symbol {
  background-color: rgb(255, 94, 94);
}
article, div, nav {
    margin: 20px;
    border: 1px solid rgb(61, 85, 104);
  }
  
  nav {
    display: inline-block;
    width: 250px;
  }
  
  nav>ul {
    list-style-type: none;
  }
  
  nav a {
    text-decoration: none;
    color: rgb(61, 85, 104);
  }
  
  nav a:hover {
    color: rgb(0, 100, 182);
  }
  
form>* {
    display: block;
    width: 250px;
    box-sizing: border-box;
    margin-bottom: 5px;
    padding: 5px;
  }
  
